import React from 'react';
import './Card.css';
import { useSpring, animated } from 'react-spring';
import { useDrag } from 'react-use-gesture';

const Card = ({title}) => {
	const arrayTitle = title.split('|');
	return (
		<div className='card-question__inner'>
			<div className='card-question__text_top card-question__text'>{arrayTitle[0]}</div>
			<div className='card-question__text_middle card-question__text'>НО</div>
			<div className='card-question__text_bottom card-question__text'>{arrayTitle[1]}</div>
		</div>
	);
}

const CardQuestion = (props) => {
	const [{ x, scale, opacity, backgroundColor, answer }, set] = useSpring(() => ({ x: 0, scale: 1, opacity: 0, backgroundColor: '', answer: ''}))
	const cardRef = React.createRef(null);
	const delta = 1.7;
	const moveToDelta = 100;

	const leave = (set, propsLeave) => {
		propsLeave();
		setTimeout(() => {
			set({x: 0, scale: 1, opacity: 0, backgroundColor: '', answer: ''});
		}, 1000);
	}

	const bind = useDrag(({ down ,movement: [mx, _] }) => {
		const widthCard = cardRef.current.offsetWidth;
		const halfWidth = Math.floor(widthCard / 5);
		const isRight = mx > halfWidth;
		const isLeft = mx < -halfWidth;
		const moveTo = mx * delta;
		const scale = down ? 0.88 : 1;
		const isLeave = Math.abs(mx) > widthCard;

		if(isRight) {
			set({
				x: down ? moveTo : widthCard + moveToDelta,
				scale,
				backgroundColor: down ? 'rgb(0,255,0)' : '',
				answer: 'Нет',
			});

			if(!down) {
				leave(set, props.isLeave)
				return;
			}
		}

		if(isLeft) {
			set({
				x: down ? moveTo : -widthCard - moveToDelta,
				scale,
				backgroundColor: down ? 'rgb(255,0,0)' : '',
				answer: 'Да',
			});

			if(!down) {
				leave(set, props.isLeave)
				return;
			}
		}

		if(!isRight && !isLeft) {
			set({
				x: down ? moveTo : 0,
				scale,
				backgroundColor: '',
				answer: '',
			});
		}

		set({
			opacity: down ? Math.abs(mx/widthCard * delta * 2) : 0,
		})
	});

	return (
		<animated.div ref={cardRef} className={'card-question'} {...bind()} style={{x,scale}} >
			<animated.div style={{opacity, backgroundColor}} className={'card-question__choose-blur'}>{answer}</animated.div>
			<Card {...props}/>
		</animated.div>
	)
};

export default CardQuestion;