import React from 'react';
// Icons for menu
import Icon28HomeOutline from '@vkontakte/icons/dist/28/home_outline';
import Icon28FavoriteOutline from '@vkontakte/icons/dist/28/favorite_outline';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
//Vk Components
import { Tabbar, TabbarItem } from '@vkontakte/vkui';


const Icon28Question = (props) => {
  return (
    <div className={'TabbarItem__icon'}>
      <div className={'Icon Icon--28 Icon--w-28 Icon--h-28'} style={{width: '28px', height: '28px'}}>
        <svg width="28" height="28" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M18.7352 4.80524C17.3285 3.63871 15.4717 3 13.5023 3c-1.9693 0-3.82616.64315-5.23284 1.80524C6.8065 6.01613 6 7.64395 6 9.3871v.33709c0 .19517.1688.35481.37512.35481h2.2507c.20631 0 .37511-.15964.37511-.35481V9.3871c0-1.95605 2.02097-3.54839 4.50137-3.54839 2.4805 0 4.5014 1.59234 4.5014 3.54839 0 1.3794-1.0315 2.6435-2.6305 3.2246-.994.3593-1.838.9891-2.4429 1.8141-.6143.8427-.9331 1.854-.9331 2.8786v.9537c0 .1951.1688.3548.3751.3548h2.2507c.2063 0 .3751-.1597.3751-.3548v-1.0069c.0024-.4306.142-.8505.4004-1.2049.2585-.3545.6239-.6271 1.0485-.7822C19.2135 14.2573 21 11.9508 21 9.3871c.0047-1.74315-.8018-3.37097-2.2648-4.58186zM11.6267 23.2258c0 .4706.1977.9218.5494 1.2546.3517.3327.8288.5196 1.3262.5196.4975 0 .9745-.1869 1.3263-.5196.3517-.3328.5493-.784.5493-1.2546 0-.4705-.1976-.9218-.5493-1.2545-.3518-.3328-.8288-.5197-1.3263-.5197-.4974 0-.9745.1869-1.3262.5197-.3517.3327-.5494.784-.5494 1.2545z"
            fill={true ? '#99A2AD' : '#2975cc' }/>
        </svg>
      </div>
    </div>
  );
}

const BottomMenu = () => {
  return (
    <Tabbar>
      <TabbarItem
        onClick={() => { }}
        selected={true}
        data-story='home'
        text="Главная">
        <Icon28HomeOutline />
      </TabbarItem>
      <TabbarItem
        onClick={() => { }}
        selected={false}
        data-story='createQuestion'
        text="Создать вопрос">
        <Icon28Question/>
      </TabbarItem>
      <TabbarItem
        onClick={() => { }}
        selected={false}
        data-story='top'
        text="Рейтинг">
        <Icon28FavoriteOutline />
      </TabbarItem>
      <TabbarItem
        onClick={() => { }}
        selected={false}
        data-story='settings'
        text="Настройки">
        <Icon28SettingsOutline />
      </TabbarItem>
    </Tabbar>
  );
}

export default BottomMenu;