import React, { useState } from 'react';
import CardQuestion from '../Card';

const arrayCards = [
  {
    id: 12321,
    title: "You can completely remake your body into exactly how you want to look and nothing you do will change it (lack of exercise, bad eating, etc.) | If anyone finds out that you remade yourself all the changes go away",
    like: 123,
    dislike: 321,
    yes: 1,
    no: 0,
  },
  {
    id: 321,
    title: "Ты умеешь летать со скоростью самолета | При использовании данной способности ты блюешь",
    like: 333,
    dislike: 100,
    yes: 1,
    no: 0,
  },
  {
    id: 111,
    title: "Ты получаешь зарплату в 1 млн. руб. | Твой начальник ебет тебя раз в месяц",
    like: 111,
    dislike: 222,
    yes: 1,
    no: 0,
  },
  {
    id: 333,
    title: "Все девочки в классе тебя любят | Мальчики тоже...",
    like: 31,
    dislike: 100,
    yes: 1,
    no: 0,
  }
];

const ListQuestions = (props) => {

  const [activeCard, setActiveCard] = useState(arrayCards[0]);


  const cardLeave = () => {
    //updateQuestion(activeCardId,yesOrNow,likeDisLike);
    //setStatisticQuestion();
    arrayCards.shift();
    props.setModal('statistic');
    if (arrayCards.length < 2) getNewCard(); 
    if(!arrayCards.length) {
      emptyQuestion();
      return;
    }
    setActiveCard(arrayCards[0]);
  }

  const emptyQuestion = () => alert('Вопросы закончились');

  const getNewCard = () => console.log('fetch card');

  return (
    <>
      {<CardQuestion {...activeCard} isLeave={cardLeave} />}
    </>
  );
};

export default ListQuestions;