const { createSlice } = require("@reduxjs/toolkit");

const initialStateApp = {
  isLoading: false,
}

const appSlice = createSlice({
  name: "app",
  initialState: initialStateApp,
  reducers: {
    appLoaded: state => state.isLoading = false,
    appLoading: state => state.isLoading = true,
  }
})

export const {
  appLoaded,
  appLoading
} = appSlice.actions

export default appSlice.reducer;