import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import { Div, Title } from '@vkontakte/vkui';
import './Home.css';
import ListQuestions from '../components/CardList';

const Home = ({ id, go, setModal}) => {

	const HeaderTitle = () => {
		return (<Div>
			<Title level="1" 
				weight="medium" 
				style={{ color: '#000' }}>Главная</Title>
		</Div>);
	};

	const getHeightForContent = () => {
		const heightHeader = document.querySelector('.PanelHeader')?.offsetHeight;
		const heightBottomMenu = document.querySelector('.Tabbar ')?.offsetHeight;
		return `calc(100vh - ${heightBottomMenu | 0}px - ${heightHeader | 0}px - 24px)`;
	};

	return (
		<Panel id={id}>
			<PanelHeader
				left={<HeaderTitle/>}
			/>
			<Div className='content' style={{height: getHeightForContent()}}>
				<ListQuestions setModal={setModal}/>
			</Div>
		</Panel>
	);
}

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
